import {Component, OnInit, ViewChild} from '@angular/core';
import {BsModalRef} from 'ngx-bootstrap/modal';
import {TabsetComponent} from 'ngx-bootstrap/tabs';

@Component({
  selector: 'app-aanvraag',
  templateUrl: './aanvraag.component.html',
  styleUrls: ['./aanvraag.component.scss']
})
export class AanvraagComponent implements OnInit {

  @ViewChild('wizard', { static: true }) wizardTabs: TabsetComponent;

  constructor(
    private modalRef: BsModalRef,
  ) { }

  ngOnInit() {
  }

  public close(): void {
    this.modalRef.hide();
  }

  public nextTab(): void {
    const index = this.wizardTabs.tabs.findIndex(t => t.active);
    if (index + 1 >= this.wizardTabs.tabs.length) {
      this.close();
      return;
    }
    this.wizardTabs.tabs[index + 1].active = true;
  }

  public previousTab(): void {
    const index = this.wizardTabs.tabs.findIndex(t => t.active);
    if (index === 0) {
      this.close();
      return;
    }
    this.wizardTabs.tabs[index - 1].active = true;
  }

}
