import {Component, OnInit} from '@angular/core';
import {BsModalService} from 'ngx-bootstrap';
import {AanvraagComponent} from './aanvraag/aanvraag.component';

@Component({
  selector: 'app-zelfstandigen',
  templateUrl: './zelfstandigen.component.html',
  styleUrls: ['./zelfstandigen.component.scss']
})
export class ZelfstandigenComponent implements OnInit {

  constructor(
    private modalService: BsModalService,
  ) { }

  ngOnInit() {
  }

  public openRequestModal(): void {
    this.modalService.show(AanvraagComponent, {
      class: 'modal-lg',
      initialState: {
      },
    });
  }

}
