import { Component, Input, OnInit } from '@angular/core';

@Component({
  selector: 'app-link-git',
  templateUrl: './link-git.component.html',
  styleUrls: ['./link-git.component.scss']
})
export class LinkGitComponent implements OnInit {

  @Input() public gitPage: string;
  public gitUri: string;

  constructor() { }

  ngOnInit() {
    this.gitUri = 'https://git.global.dns/projects/FE/repos/acerta-ui/browse/library/src/app/' + this.gitPage;
  }

}
