import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import {TranslateModule} from '@ngx-translate/core';
import {SharedModule} from '../shared/shared.module';
import {AcertaAppComponent} from './acerta-app/acerta-app.component';
import {LciSearchComponent} from './lci-search/lci-search.component';
import {ZelfstandigenComponent} from './zelfstandigen/zelfstandigen.component';
import {AanvraagComponent} from './zelfstandigen/aanvraag/aanvraag.component';
import {AansluitingComponent} from './bedrijfsleiders/aansluiting/aansluiting.component';
import { CardListComponent } from './card-list/card-list.component';

const routes: Routes = [
    { path: 'acertapp', component: AcertaAppComponent },
    { path: 'lci-search', component: LciSearchComponent },
    { path: 'zelfstandigen', component: ZelfstandigenComponent },
    { path: 'bedrijfsleiders', component: AansluitingComponent },
    { path: 'card-lijst', component: CardListComponent },
    { path: '', pathMatch: 'full' , redirectTo: '/acertapp'},
];

@NgModule({
  imports: [
    SharedModule,
    RouterModule.forChild(routes),
    TranslateModule.forChild(),
  ],
  declarations: [
    AcertaAppComponent,
    LciSearchComponent,
    ZelfstandigenComponent,
    AanvraagComponent,
    AansluitingComponent,
    CardListComponent,
  ],
  entryComponents: [
    AanvraagComponent,
  ],
})
export class LayoutModule { }
